@media (max-width: 900px) {
  .login_content {
    flex-direction: column;
    max-width: 100%;
    min-height: 100vh;
  }
  .login_content_left,
  .login_content_right {
    flex: unset;
    width: 100%;
    padding: 32px 16px;
    border-radius: 0;
  }
  .login_content_left {
    border-bottom: 1px solid #eee;
    border-right: none;
  }
  .login_content_right {
    padding-top: 24px;
  }
}

@media (max-width: 600px) {
  .login_main {
    padding: 0 4vw;
  }
  .login_content {
    box-shadow: none;
    border-radius: 0;
  }
  .login_content_left,
  .login_content_right {
    padding: 20px 0;
  }
  .login_right_top {
    max-width: 100%;
  }
  .login_button {
    font-size: 15px;
    padding: 12px;
  }
  .login_left_list {
    font-size: 15px;
  }
}
